<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="categories"
      :search="search"
      v-model="selected"
      show-select
      class="elevation-2 ma-2"
      :loading="loading"
      loading-text="Loading... Please wait"
    >
      <!-- <template v-slot:top> 在表格顶部添加的内容 -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>分类管理</v-toolbar-title>

          <v-divider class="mx-4" inset vertical>
            <!-- vertical 用于指定垂直方向的分隔线 -->
          </v-divider>
          <v-btn
            class="red darken-2 white--text mr-2"
            @click="deleteAll"
            :disabled="selected.length <= 0"
          >
            <v-icon>mdi-delete</v-icon>
            Delete Selected
          </v-btn>
          <v-btn
            class="amber darken-2 white--text"
            @click="resetSelectedLevel"
            :disabled="selected.length <= 0"
          >
            <v-icon>mdi-restore</v-icon>
            Reset Selected Level
          </v-btn>
          <v-spacer></v-spacer>
          <!-- 搜索文本框 -->
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
            class="mx-3"
          ></v-text-field>
          <!-- 添加 和 修改 对话框 start -->
          <v-dialog v-model="dialog" max-width="800px">
            <!-- dialog 内的 template 为触发对话框的元素，例如按钮 -->
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="success" dark class="mb-2" v-bind="attrs" v-on="on">
                <v-icon>mdi-plus</v-icon>
                New Item
              </v-btn>
            </template>
            <!-- dialog 内的 </template> 之后为对话框显示的内容 -->
            <v-card>
              <!-- 对话框的标题在 添加 和 修改 时显示不同的颜色 -->
              <v-card-title
                class="darken-2 white--text"
                :class="editedIndex === -1 ? 'teal' : 'amber'"
              >
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col :cols="formCols">
                      <!-- Title输入框 -->
                      <v-text-field
                        v-model="editedItem.title"
                        label="Category Title"
                        :counter="50"
                        clearable
                      ></v-text-field>
                    </v-col>
                    <v-col cols="2" v-if="editedIndex > -1">
                      <!-- 只有在修改时才显示的 level 数字框， type="number"用于显示数字微调 -->
                      <v-text-field
                        v-model="editedItem.level"
                        label="Level"
                        value="0"
                        type="number"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col cols="12">
                      <!-- 多行文本框 -->
                      <v-textarea
                        rows="4"
                        v-model="editedItem.description"
                        label="Category Description"
                        counter
                        outlined
                      ></v-textarea>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="success darken-1" text @click="save">
                  Save
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <!-- 添加 和 修改 对话框 end -->
          <!-- 确认删除对话框 start -->
          <v-dialog v-model="dialogDelete" max-width="500">
            <v-card>
              <v-card-title class="text-h5 red darken-2 white--text">
                Delete Item?
              </v-card-title>
              <v-card-text class="pa-8 text-h6">
                {{ deleteConfirmInfo }}
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">
                  Cancel
                </v-btn>
                <v-btn color="success darken-1" text @click="deleteItemConfirm">
                  OK
                </v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <!-- 确认删除对话框 end -->
        </v-toolbar>
      </template>
      <!-- 下面这个  template 使用 v-slot 指定内容显示到value="actions" 对应的单元格 -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          dense
          class="mr-2"
          color="amber"
          @click="editItem(item)"
          title="Edit Item"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          color="red darken-2"
          dense
          @click="deleteItem(item)"
          title="Delete Item"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
    <v-row></v-row>
  </v-container>
</template>

<script>
import moment from "moment"; //日期处理类库
import CategoryService from "@/service/category_service.js";
export default {
  name: "cagegory-manager",
  data: () => ({
    loading: false, // 显示正在载入的进度条
    dialog: false, // 用于显示 添加 或 修改 对话框
    dialogDelete: false, // 删除确认对话框
    deleteConfirmInfo: "", // 删除确认对话框显示的信息
    editedIndex: -1, // 用于判断 添加 或 修改 的标志；-1表示添加，否则为在 categories中的索引
    // 添加或修改绑定到对话框上的数据
    editedItem: {
      title: "",
      description: "",
      level: 0,
    },
    // 用于添加或修改结束后，还原对话框的初始值
    defaultItem: {
      title: "",
      description: "",
      level: 0,
    },
    page: 1,
    pageSize: 10,
    totalPages: 1,
    totalVisible: 10,
    pageItems: [10, 20, 30, 50, 100],
    categories: [], // 所有的分类数据
    // 表格的表头; text: 表头显示文字；value: 单元对应的值；
    // sortable: 是否允许排序；align：对齐; width: 指定固定的宽度
    headers: [
      { text: "Title", value: "title", sortable: true, align: "start" },
      { text: "Description", value: "description", sortable: false },
      {
        text: "LastEditor",
        value: "lastEditor.username",
        sortable: true,
        width: 150,
      },
      { text: "Level", value: "level", sortable: true, width: 80 },
      { text: "Count", value: "topicCount", sortable: true, width: 90 },
      { text: "CreatedAt", value: "createdAt", sortable: true, width: 130 },
      { text: "UpdatedAt", value: "updatedAt", sortable: true, width: 130 },
      {
        text: "Actions",
        value: "actions",
        sortable: false,
        width: 80,
        align: "center",
      },
    ],
    selected: [], //多选框选中的项
    search: "", // 用于搜索功能，分别绑定到搜索框和数据表组件
  }),
  // 加载完毕时执行的代码
  mounted() {
    moment.locale("zh-cn"); // 设置日期地区
    this.getCategories();
  },
  methods: {
    // 获取所有的分类数据
    getCategories() {
      this.loading = true; // 显示载入进度条
      this.categories = []; // 清空原有数据
      CategoryService.fetchAll('level')
        .then((resp) => {
          for (let item of resp) {
            this.categories.push(this.fromJson(item));
          }
          console.log(this.categories);
          this.loading = false; // 停止进度条显示
        })
        .catch((error) => {
          this.loading = false; // 停止进度条显示
          //
        });
    },

    // 显示删除确认对话框
    deleteItem(item) {
      console.log("deleteItem: ", item);
      this.editedIndex = this.categories.indexOf(item); // 查找对象的位置
      this.editedItem = Object.assign({}, item); // 合并对象
      this.deleteConfirmInfo = "Are you sure you want to delete this item?";
      this.dialogDelete = true;
    },

    // 显示批量删除确认对话框
    deleteAll() {
      console.log("delete all: ", this.selected);
      let count = this.selected.length;
      if (count) {
        this.deleteConfirmInfo = `Are you sure you want to delete ${count} items?`;
        this.dialogDelete = true;
      }
    },

    // 在删除确认对话框中确认删除
    deleteItemConfirm() {
      if (this.selected.length) {
        CategoryService.deleteAll(this.selected)
          .then((resp) => {
            this.getCategories(); //重新载入所有数据
            this.selected = []; // 清除多选框
          })
          .catch((error) => {
            //
          });
      } else {
        let item = this.categories.splice(this.editedIndex, 1)[0];
        console.log(item);
        CategoryService.delete(item.id)
          .then((resp) => {
            console.log("deleteItemConfirm: ", resp);
            //
          })
          .catch((error) => {
            //
          });
      }
      this.closeDelete();
    },

    // 在删除确认对话框中取消删除
    closeDelete() {
      this.dialogDelete = false;
      // this.$nextTick()回调函数将在 DOM 更新完成后被调用
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    // 显示修改对话框
    editItem(item) {
      console.log("editItem: ", item);
      // 查询要修改记录在 categories 中的索引值
      this.editedIndex = this.categories.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    // 添加 或 修改 的保存功能
    save() {
      if (this.editedIndex > -1) {
        // 更新当前显示数据中的 updatedAt，leancloud中的updatedAt 会自动更新，不用上传到服务器
        this.editedItem.updatedAt = moment(Date.now()).format("lll");
        this.editedItem.lastEditor = CategoryService.currentUser(); // 最后的修改者
        // Object.assign()合并修改后的数据到内存中的数组，更新显示的内容
        let item = Object.assign(
          this.categories[this.editedIndex],
          this.editedItem
        );

        /**
         * 更新服务器上的内容, 修改的数据中不要包含 id 字段，
         * 因为leancloud 实际使用的主键名为：objectId,
         * 包含 id 字段会导致 Class 中多出一个名字为 id 的列名。
         */
        CategoryService.update(item.id, {
          title: item.title,
          description: item.description,
          level: Number(item.level), // 转为数字类型
          lastEditor: CategoryService.currentUser(), // 最后的修改者
        });
      } else {
        // 实例化分类对象
        let category = {
          title: this.editedItem.title.trim(), // 分类标题， 去掉前后空格
          description: this.editedItem.description, // 分类的说明
          level: 0, // 用于手工排序
          count: 0, // 分类中包含的主题数量
          createdBy: CategoryService.currentUser(), // 分类创建者
          lastEditor: CategoryService.currentUser(), // 最后的修改者
        };

        // new Category(this.editedItem.title.trim(), this.editedItem.description);
        if (category.title) {
          // 检查要添加的分类标题是否已经存在
          CategoryService.exists(category.title)
            .then((result) => {
              // 如果要添加的标题不存在才继续
              if (!result) {
                CategoryService.create(category)
                  .then((item) => {
                    // 添加数据到当前的显示表格
                    this.categories.push(this.fromJson(item));
                  })
                  .catch((error) => {
                    console.log(error);
                  });
              } else {
                console.log("the title is exists.");
              }
            })
            .catch((error) => {
              //
            });
        } else {
          console.log("the title is null.");
        }
      }
      this.close();
    },

    //  批量重置选中项的 level 值为 0 , 可以考虑增加一个类似 删除确认 的对话框
    resetSelectedLevel() {
      CategoryService.batchUpdate(this.selected, "level", 0)
        .then((resp) => {
          this.selected = []; // 清除选中项
          console.log(resp);
          // this.getCategories();  // 更新数据
          for (let item of resp) {
            let index = this.categories.findIndex((c) => c.id === item.id);
            // 必须将返回的数据用 this.fromJson(item) 格式化为表格中的数据才能更新显示的内容
            Object.assign(this.categories[index], this.fromJson(item));
          }
        })
        .catch((error) => {});
    },

    // 关闭对话框，并还原对话框数据为初始值
    close() {
      this.dialog = false; // 关闭对话框
      // this.$nextTick()将回调延迟到下次 DOM 更新循环之后执行
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    // 格式化读取到的一条记录用于表格显示
    fromJson(item) {
      return {
        id: item.id,
        title: item.get("title"),
        description: item.get("description"),
        level: item.get("level"),
        topicCount: item.get("count"),
        createdAt: moment(item.createdAt).format("lll"),
        updatedAt: moment(item.updatedAt).format("lll"),
        // 指向另一个表的字段，所以要先判断，如果为空，则取值会抛出异常，
        createdBy: item.get("createdBy")
          ? this.getUser(item.get("createdBy"))
          : "",
        lastEditor: item.get("lastEditor")
          ? this.getUser(item.get("lastEditor"))
          : "",
      };
    },

    // 获取用户表中各个字段的值
    getUser(user) {
      // console.log(user);
      return {
        id: user.id,
        username: user.get("username"),
        email: user.get("email"),
      };
    },
  },

  // 计算属性：函数名当作变量使用
  computed: {
    // 添加或修改对话框的标题文字
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
    // 添加 或 修改 时 对话框 Title 的宽度
    // 添加时不用显示 Level，Title 宽度为 cols="12"
    // 修改时需要显示 Level，Title 宽度为 cols="10"
    formCols() {
      return this.editedIndex === -1 ? 12 : 10;
    },
  },

  // 观察属性用于监控变量的值是否发生变化
  watch: {
    // 监控 data 中的变量：dialog，val 为有变化后的值
    dialog(val) {
      val || this.close(); // 如果是对话框以外空白的地方关闭对话框时，还原对话框的状态
    },

    dialogDelete(val) {
      val || this.close(); // 如果是对话框以外空白的地方关闭对话框时，还原对话框的状态
    },
  },
};
</script>

